* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)),
    url('../images/world.png');
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  min-width: 320px;
  margin-bottom: 16px;
}

.card-head {
  display: flex;
  align-items: center;
  font-size: 36px;
}

.card-head__flag {
  height: 100%;
  width: 60px;
  margin-right: 10px;
}

.card-head__name {
  margin: 0;
}

.card-body {
  font-size: 24px;
}

.country-list__item {
  list-style: none;
}

.country-list__item {
  display: flex;
  font-size: 26px;
  align-items: center;
}

.country-list__flag {
  height: 100%;
  width: 36px;
  margin-right: 10px;
}

.country-list__name {
  margin: 0;
}
